import Vue from 'vue'
import Router from 'vue-router'
import Home from '../Home'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '*',
      name: 'Home',
      component: Home,
      redirect: '/home/index',
      children: [{
        path: 'index',
        component: resolve => require(['../views/Index'], resolve)
      }, {
        path: 'famousTeacher',
        component: resolve => require(['../views/FamousTeacher'], resolve)
      }, {
        path: 'famousTeacherInfo',
        component: resolve => require(['../views/FamousTeacherInfo'], resolve)
      }, {
        path: 'lessonList',
        component: resolve => require(['../views/LessonList'], resolve)
      }, {
        path: 'culture',
        component: resolve => require(['../views/Culture'], resolve)
      }, {
        path: 'news',
        component: resolve => require(['../views/News'], resolve)
      }, {
        path: 'community',
        component: Home,
        children: [{
          path: '/',
          component: resolve => require(['../views/Community'], resolve)
        }, {
          path: 'release',
          component: resolve => require(['../views/Release'], resolve)
        }, {
          path: 'comment',
          component: resolve => require(['../views/Comment'], resolve)
        }, {
          path: 'users',
          component: resolve => require(['../views/Users'], resolve)
        }, {
          path: 'concerned',
          component: resolve => require(['../views/Concerned'], resolve)
        }, {
          path: 'dynamic',
          component: resolve => require(['../views/Dynamic'], resolve)
        }, {
          path: 'fans',
          component: resolve => require(['../views/Fans'], resolve)
        }, {
          path: 'chat',
          component: resolve => require(['../views/Chat'], resolve)
        }]
      }, {
        path: 'teachers',
        component: Home,
        children: [{
          path: '/',
          component: resolve => require(['../views/Teachers'], resolve)
        }, {
          path: 'userInfor',
          component: resolve => require(['../views/UserInfor'], resolve)
        }, {
          path: 'gymInfo',
          component: resolve => require(['../views/GymInfo'], resolve)
        }, {
          path: 'yearCard',
          component: resolve => require(['../views/cards/YearCard'], resolve)
        }, {
          path: 'timeCard',
          component: resolve => require(['../views/cards/TimeCard'], resolve)
        }, {
          path: 'personalCard',
          component: resolve => require(['../views/cards/PersonalCard'], resolve)
        }, {
          path: 'lessonInfor',
          component: resolve => require(['../views/LessonInfor'], resolve)
        }, {
          path: 'appointment',
          component: resolve => require(['../views/Appointment'], resolve)
        }, {
          path: 'search',
          component: resolve => require(['../views/Search'], resolve)
        }]
      }, {
        path: 'prictice',
        component: Home,
        children: [{
          path: '/',
          component: resolve => require(['../views/Prictice'], resolve)
        }, {
          path: 'pricticeDetails',
          component: resolve => require(['../views/PricticeDetails'], resolve)
        }]
      }, {
        path: 'mine',
        component: Home,
        children: [{
          path: '/',
          component: resolve => require(['../views/Mine'], resolve)
        }, {
          path: 'myWallet',
          component: resolve => require(['../views/User/MyWallet'], resolve)
        }, {
          path: 'myLesson',
          component: resolve => require(['../views/User/MyLesson'], resolve)
        }, {
          path: 'timeSelect',
          component: resolve => require(['../views/User/TimeSelect'], resolve)
        }, {
          path: 'lessonSelect',
          component: resolve => require(['../views/User/LessonSelect'], resolve)
        }, {
          path: 'lessonManage',
          component: resolve => require(['../views/User/LessonManage'], resolve)
        }, {
          path: 'myVideo',
          component: resolve => require(['../views/User/Myvideo'], resolve)
        }, {
          path: 'uploadVideo',
          component: resolve => require(['../views/User/UploadVideo'], resolve)
        }, {
          path: 'myCollection',
          component: resolve => require(['../views/User/MyCollection'], resolve)
        }, {
          path: 'myRegiment',
          component: resolve => require(['../views/User/MyRegiment'], resolve)
        }, {
          path: 'myCards',
          component: resolve => require(['../views/User/MyCards'], resolve)
        }, {
          path: 'myYearCard',
          component: resolve => require(['../views/User/MyYearCard'], resolve)
        }, {
          path: 'myTimeCard',
          component: resolve => require(['../views/User/MyTimeCard'], resolve)
        }, {
          path: 'myPersonalCard',
          component: resolve => require(['../views/User/MyPersonalCard'], resolve)
        }, {
          path: 'videoPlayer',
          component: resolve => require(['../views/User/VideoPlayer'], resolve)
        }, {
          path: 'setting',
          component: resolve => require(['../views/Setting'], resolve)
        }, {
          path: 'advice',
          component: resolve => require(['../views/User/Advice'], resolve)
        }, {
          path: 'myprofile',
          component: resolve => require(['../views/User/Myprofile'], resolve)
        }, {
          path: 'about',
          component: resolve => require(['../views/User/About'], resolve)
        }, {
          path: 'changePwd',
          component: resolve => require(['../views/User/ChangePwd'], resolve)
        }, {
          path: 'lessonPrice',
          component: resolve => require(['../views/User/LessonPrice'], resolve)
        }, {
          path: 'signIn',
          component: resolve => require(['../views/User/SignIn'], resolve)
        }, {
          path: 'henghaTips',
          component: resolve => require(['../views/User/HenghaTips'], resolve)
        }, {
          path: 'recharge',
          component: resolve => require(['../views/User/Recharge'], resolve)
        }, {
          path: 'appiontList',
          component: resolve => require(['../views/User/AppiontList'], resolve)
        }, {
          path: 'createRegiment',
          component: resolve => require(['../views/User/CreateRegiment'], resolve)
        }, {
          path: 'regimentList',
          component: resolve => require(['../views/User/regimentList'], resolve)
        }]
      }, {
        path: 'login',
        component: resolve => require(['../views/login/Login'], resolve)
      }, {
        path: 'enroll',
        component: Home,
        children: [{
          path: '/',
          component: resolve => require(['../views/enroll/Enroll'], resolve)
        }, {
          path: 'identify',
          component: resolve => require(['../views/enroll/Identify'], resolve)
        }, {
          path: 'validate',
          component: resolve => require(['../views/enroll/ValidateEnsure'], resolve)
        }, {
          path: 'coachStep1',
          component: resolve => require(['../views/enroll/CoachStep1'], resolve)
        }, {
          path: 'coachStep2',
          component: resolve => require(['../views/enroll/CoachStep2'], resolve)
        }, {
          path: 'coachStep3',
          component: resolve => require(['../views/enroll/CoachStep3'], resolve)
        }, {
          path: 'gymAuthentication',
          component: resolve => require(['../views/enroll/GymAuthentication'], resolve)
        }, {
          path: 'introduce',
          component: resolve => require(['../views/enroll/Introduce'], resolve)
        }]
      }]
    }
  ]
})



